<template>
  <div id="bottomRight">
    <div class="bg-color-black">
      <div class="d-flex pt-2 pl-2">
        <span>
          <icon name="chart-line" class="text-icon"></icon>
        </span>
        <div class="d-flex">
          <span class="fs-xl text mx-2" style="font-size: 15px">出校晚归信息</span>
        </div>
      </div>
      <div class="d-flex jc-center body-box">
        <dv-scroll-board class="dv-scr-board" :config="config" />
      </div>
    </div>
  </div>
</template>

<script>
import {getLate} from "@/axios/api";
export default {
  data() {
    return {
      config: {}
    }
  },mounted() {
    this.getLate()
  },methods:{
    async getLate() {
      var data = []
      let clas = await getLate().then(res => res.data)
      if (clas.length > 0) {
        for (var i = 0; i < clas.length; i++) {
          data[i] = ([clas[i][0], clas[i][1], "<span  class='colorBlue'>" + clas[i][2] + "</span>"])
        }
        this.config = {
          header: ['日期', '姓名', '班级'],
          data: data,
          rowNum: 7, //表格行数
          headerHeight: 35,
          headerBGC: '#0f1325', //表头
          oddRowBGC: '#0f1325', //奇数行
          evenRowBGC: '#171c33', //偶数行
          index: true,
          columnWidth: [50],
          align: ['center']
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
$box-height: 520px;
$box-width: 425px;
#bottomRight {
  padding: 20px;
  padding-top: 20px;
  height: $box-height;
  width: $box-width;
  border-radius: 5px;
  .bg-color-black {
    height: $box-height - 30px;
    border-radius: 10px;
  }
  .text {
    color: #c3cbde;
  }
  .body-box {
    border-radius: 10px;
    overflow: hidden;
    .dv-scr-board {
      width: 350px;
      height: 450px;
    }
  }
}
</style>
